@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
/*Poppins*/

/*Global*/
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  font-family: 'Poppins', sans-serif;
}

/*Main Sign up Page*/
.sign-up{
  display: flex;
}
.welcome-area{
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
  align-items: center;
  background: linear-gradient(#3EA4DC, #3EDC95 90%);
  color: white;
  font-size: 1.4rem;
}
.welcome-area i{
  font-size: 100px;
}
.sign-up-area{
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
.sign-up-area input {
  margin-left: 25px;
  margin-top: 20px;
  padding: 10px 40px 10px 10px;
  transition: 0.3s;
  border: solid 1px;
  border-radius: 10px;
}
.sign-up-area input:focus {
  padding: 10px 5px 10px 10px;
}
.btn{
  display: flex;
  justify-content: center;
}
.submit-btn{
  padding: 10px 20px;
  background: linear-gradient(to right, #3EA4DC,#3EDC95);
  color: white;
  cursor: pointer;
  border: none;
  margin-top: 20px;
  font-size: 16px;
  border-radius: 10px;
  transition: 0.3s;
}
.submit-btn:active {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.container {
    width: 90%;
    margin: 0 auto;
}
/*Header*/
header {
    display: flex;
    justify-content: space-between;
    height: 70px;
    align-items: center;
}
.logo-container {
    display: flex;
    align-items: center;
    color: #3EA4DC;
}
.logo-container i {
    font-size: 60px;
}
.nav-links {
    display: flex;
    list-style: none;
}
nav li{
    text-decoration: none;
    color: #3EA4DC;
    margin-left: 55px;
    font-size: 20px;
    cursor: pointer;
}
.burger{
    display: none;
}
.burger div {
    height: 3px;
    width: 30px;
    background-color: white;
    margin-top: 3px;
}

/*Main area*/
.main {
    display: flex;
    justify-content: space-between;
}
.main-sidebar {
    width: 30%;
    flex: 0 1;
}
.main-feed {
    width: 50%;
    align-items: center;
    display: flex;
    flex-direction: column;
}
.recommended {
    width: 20%;
}
.post {
    border-bottom: 1px solid #3EDC95;
}
.post-container {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
}
.post-input {
    border: none;
    border-left: 2px solid black;
    padding: 20px;
    font-size: 25px;
}
.user-img {
    height: 100px;
    margin-right: 20px;
}
.posting-options {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.upload-img {
    cursor: pointer;
    margin-right: 50px;
}
.post-btn {
    padding: 10px 15px;
    color: white;
    border: none;
    border-radius: 20px;
    background: linear-gradient(to right, #3EA4DC, #3EDC95);
    cursor: pointer;
    transition: 0.3s;
    margin-bottom: 10px;
}
.post-btn:active {
    -webkit-transform: scale(.8);
            transform: scale(.8);
}
.sidebar {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 90vh;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    width: auto;
}
.sidebar-component {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 20px;
    transition: 0.3s;
}
.sidebar-component:hover {
    background: linear-gradient(to right, #3EA4DC, #3EDC95);
    color: white;
    border-radius: 20px;
    cursor: pointer;
}
.sidebar-component h1 {
    margin-left: 10px;
    font-size: 23px;
}
.sidebar-component i {
    font-size: 30px;
}
.feed-item {
    width: 90%;
    margin-top: 15px;
    border: 1px solid grey;
    min-height: 30px;
    padding: 20px 10px 30px 10px;
    border-radius: 20px;
}
.userpfp-and-username {
    display: flex;
    align-items: center;
}
.feed-item h1 {
    margin-left: 20px;
    color: #3EA4DC;
}
.pfp {
    border-radius: 100%;
    height: 60px;
    width: 60px;
    margin-left: 20px;
}
.time-posted {
    width: 70%;
    margin: auto;
    color: grey;
}
.f-img-and-txt {
    margin-top: 10px;
    margin-bottom: 50px;
}
.feed-text {
    width: 70%;
    margin: auto;
}
.feed-image {
    width: 90%;
    height: 300px;
    border-radius: 20px;
    text-align: center;
    margin-top: 15px;
}
.fd-img {
    border: 1px solid black;
}
.f-img-and-txt {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.like-and-share {
    display: flex;
    justify-content: space-between;
    width: 70%;
    margin: auto;
}
.like-and-share i {
    font-size: 20px;
    color: #3EA4DC;
    cursor: pointer;
}
.recommended-component {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 90vh;
    align-items: center;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
}
.rec-item {
    display: flex;
    width: 100%;
    padding: 10px;
    background-color: rgb(236, 236, 236);
    border-radius: 20px;
}
.rec-item-img img {
    width: 50px;
    height: 50px;
    border-radius: 100%;
}
.rec-item-text h1 {
    font-size: 20px;
    margin-left: 10px;
}
.rec-item-text p{
    font-size: 16px;
    margin-left: 10px;
}
.rec-h1 {
    font-size: 30px;
    color: #3EA4DC;
}

.profile-section {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.click-or-drop{
    background: linear-gradient(to right, #3EA4DC, #3EDC95);
    cursor: pointer;
    padding: 15px 20px;
    border: none;
    border-radius: 20px;
    color: white;
    transition: 0.3s;
}
.click-or-drop:active{
    -webkit-transform: scale(.8);
            transform: scale(.8);
}
.remove-img {
    background: linear-gradient(to left, #3EA4DC, #3EDC95);
    cursor: pointer;
    padding: 15px 20px;
    border: none;
    border-radius: 20px;
    color: white;
    transition: 0.3s;
}
