.feed-item {
    width: 90%;
    margin-top: 15px;
    border: 1px solid grey;
    min-height: 30px;
    padding: 20px 10px 30px 10px;
    border-radius: 20px;
}
.userpfp-and-username {
    display: flex;
    align-items: center;
}
.feed-item h1 {
    margin-left: 20px;
    color: #3EA4DC;
}
.pfp {
    border-radius: 100%;
    height: 60px;
    width: 60px;
    margin-left: 20px;
}
.time-posted {
    width: 70%;
    margin: auto;
    color: grey;
}
.f-img-and-txt {
    margin-top: 10px;
    margin-bottom: 50px;
}
.feed-text {
    width: 70%;
    margin: auto;
}
.feed-image {
    width: 90%;
    height: 300px;
    border-radius: 20px;
    text-align: center;
    margin-top: 15px;
}
.fd-img {
    border: 1px solid black;
}
.f-img-and-txt {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.like-and-share {
    display: flex;
    justify-content: space-between;
    width: 70%;
    margin: auto;
}
.like-and-share i {
    font-size: 20px;
    color: #3EA4DC;
    cursor: pointer;
}