
.profile-section {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.click-or-drop{
    background: linear-gradient(to right, #3EA4DC, #3EDC95);
    cursor: pointer;
    padding: 15px 20px;
    border: none;
    border-radius: 20px;
    color: white;
    transition: 0.3s;
}
.click-or-drop:active{
    transform: scale(.8);
}
.remove-img {
    background: linear-gradient(to left, #3EA4DC, #3EDC95);
    cursor: pointer;
    padding: 15px 20px;
    border: none;
    border-radius: 20px;
    color: white;
    transition: 0.3s;
}